import { Component, OnInit, Input } from '@angular/core';
import { UserService } from 'src/app/services/authentication/user.service';
import { ApiUrls } from 'src/app/services/api.urls';
import { MemCacheService } from 'src/app/services/cache/mem-cache.service';

@Component({
  selector: 'app-house-inclination-map',
  templateUrl: './house-inclination-map.component.html',
  styleUrls: ['./house-inclination-map.component.css']
})
export class HouseInclinationMapComponent implements OnInit {

  locations: any[] = [];

  showFilter = true;

  @Input('noFilter') set _noFilter(val) {
    this.showFilter = false;
  }

  parties = {};

  filter = {where: {}};

  @Input('filter') set __filter(filter) {
    this.filter =  filter;
    this.load();
  }
  constructor(private userService: UserService, private memCacheService: MemCacheService) { }

  ngOnInit() {
    this.load();
  }
  load() {
    this.locations = [];
    this.userService.getCurrentUser().subscribe((user) => {
      this.userService.authenticatedGet(
        ApiUrls.ByModelProjected('statehouse', JSON.stringify({where: {stateId: user.stateId, ...this.filter.where}}),
         `{location: 1, houseNumber: 1, inclinationPartyId: 1, stateBoothId: 1}`
         )
        ).subscribe((res) => {
          res.data.forEach((house) => {
            const obj = {
              houseId: house.id,
              lat: house.location.lat,
              long: house.location.long
            };
            this.locations.push(obj);
            this.memCacheService.getItem('stateparty', house.inclinationPartyId).subscribe(party => {
              obj['flag'] = party.flag;
              obj['partyName'] = party.name;
            });
            this.userService.authenticatedGet(
              ApiUrls.ByModel('voter/count', `{where:{stateBoothId: "${house.stateBoothId}","meta.houseNumber":"${house.houseNumber}"}}`)
            ).subscribe(count => {
              obj['voterCount'] = count.data;
            });
          });
      });
    });
  }
  onFilterChanged(filtR) {
    this.filter = {where: { ...filtR}};
  }
}
