import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-vbyp-chart',
  templateUrl: './vbyp-chart.component.html',
  styleUrls: ['./vbyp-chart.component.css']
})
export class VbypChartComponent implements OnInit {
  public chartData:any;
  single: any[];
  multi: any[];

  view: any[] = [700, 400];

  // options
  showXAxis = true;
  showYAxis = true;
  gradient = false;
  showLegend = true;
  showXAxisLabel = true;
  xAxisLabel = 'Country';
  showYAxisLabel = true;
  yAxisLabel = 'Population';

  colorScheme = {
    domain: ['#3366cc','#dc3912','#ff9900','#109618','#990099','#0099c6','#dd4477','#66aa00',
    '#b82e2e','#316395','#994499','#22aa99','#aaaa11','#6633cc','#e67300','#8b0707']
  };

  constructor() { 
  }
  onSelect(event) {
    console.log(event);
  }

  ngOnInit() {
  }

}
